export interface PositionItemObject {
    id: string;
    goblin: string;
    tx_hash: string;
}

const CellItemWidget: React.FC<{ data: PositionItemObject }> = ({data}) => {
    const positionData = byIdgetItem<any>(Positions.data.position_list, data?.id, 'id');
    const GlobalItem:any = byIdgetItem<any>(GlobalConfig.goblinVo_list, data?.goblin, 'goblin');
    let history = useHistory();
    if (!positionData || !GlobalItem) return (<span/>);
    return (
        <CellItemStyle>
            <div className={'id'} style={{flex: 1}}>
                # {data?.id}
            </div>
            <div className="label">
                <div className="icons">
                    <img src={Bnbimg} alt=""/>
                    <img src={Btcimg} alt=""/>
                </div>
                <div className="right">
                    <h2>{GlobalItem.farming_pool}</h2>
                    <h4>Yield Farming</h4>
                </div>
            </div>


            <div className="income">
                <h3>$ {getBalanceNumber(positionData['position_value'])}</h3>
            </div>

            <div className="income">
                <h3>$ {getBalanceNumber(positionData['total_debt'])}</h3>
            </div>
            <div className="income">
                <h3>{(positionData['position_value'] / positionData['total_debt']).toFixed(2)} %</h3>
            </div>
            <div onClick={() => history.push(`/dashboard/farm/${data?.goblin}`,)}>
                <ButtonWidget/>
            </div>
        </CellItemStyle>
    );
}


